.reset, body, #gevinst p {
  margin: 0;
  padding: 0;
}

body {
  overflow-y: hidden;
  overflow-x: hidden;
  background-color: none;
}

@media screen and (min-width: 627px) {
  main {
    display: none;
  }
  #loadingScreen {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 0;
  }
  #loadingScreen #loadingSplash {
    position: absolute;
    top: 5vw;
    left: 25vw;
    z-index: 11;
  }
  #loadingScreen #loadingSplash img {
    width: 50vw;
  }
  #loadingScreen #loadingAssets {
    position: absolute;
    top: 40%;
    left: 15%;
    width: 70%;
    height: 60%;
    z-index: 4;
  }
  #loadingScreen #loadingAssets #loadingIcon {
    position: absolute;
    bottom: 0.8vw;
    left: 3.5vw;
    z-index: 1;
  }
  #loadingScreen #loadingAssets #loadingIcon img {
    width: 20vw;
    height: 20vw;
  }
  #loadingScreen #loadingAssets #loadingBarFrame {
    border-radius: 4vw;
    margin-top: 25vw;
    margin-left: 10vw;
    width: 50vw;
    height: 4vw;
    border: solid black 2px;
  }
  #loadingScreen #loadingAssets #loadingBarFrame #loadingBar {
    border-radius: 4vw;
    background: #ffd32a;
    background: -moz-linear-gradient(45deg, #ffd32a 0%, #ff5c3c 100%);
    background: -webkit-linear-gradient(45deg, #ffd32a 0%, #ff5c3c 100%);
    background: linear-gradient(45deg, #ffd32a 0%, #ff5c3c 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffd32a', endColorstr='#ff5c3c',GradientType=1 );
    width: 0vw;
    height: 4vw;
  }
  #loadingScreen #topLoading {
    background-color: #0c2461;
    width: 100%;
    height: 50vw;
    position: absolute;
    z-index: 1;
  }
  #loadingScreen #bottomLoading {
    background-color: #0c2461;
    width: 100%;
    height: 50vw;
    position: absolute;
    z-index: 1;
  }
}

